// Normally button styles
button
	background-color: $n
	cursor: pointer
.btn
	&__telephone
		color: #FFF
		font-size: 18px
		margin: 5px 0 0 35px
		+os
		&:hover
			color: $dark
	&__blue
		height: 48px
		border: 1px solid $blue
		-webkit-border-radius: 48px
		border-radius: 48px
		color: $blue
		font-size: 18px
		padding: 0 40px
		+tr(.3s)
		&:hover
			background-color: $blue
			color: #FFF
	&__lang
		position: relative
		display: inline-block
		vertical-align: bottom
		width: 28px
		height: 28px
		margin-right: 20px
		-webkit-border-radius: 100%
		border-radius: 100%
		border: 1px solid $blue
		+tr(.3s)
		&:hover
			+gray
		.icn-minus
			+vcenter
			width: 12px
			height: 2px
			background-color: $blue
		.icn-plus
			+rotate(0)
			position: absolute
			top: 7px
			left: 50%
			margin-left: -1px
			width: 2px
			height: 12px
			background-color: $blue
			+tr(.4s)

